*, *::before, *::after,
*:before, *:after {
  box-sizing: border-box; }

body {
  padding: 0;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  transition: all .2s ease-in-out; }
  body.initial {
    background-color: #091B28; }
  body.angular {
    background-color: #C4042F;
    color: #ffffff; }
  body.vue {
    background-color: #41BA82;
    color: #091B28; }
  body.react {
    background-color: #61DAFB;
    color: #091B28; }
  body.sass {
    background-color: #CE649A;
    color: #ffffff; }
  body.bootstrap {
    background-color: #563E7C;
    color: #ffffff; }

.main-wrapper, .header-wrapper {
  width: 100%;
  max-width: 1140px;
  margin: auto; }

.text-center {
  text-align: center; }

.main-wrapper {
  margin-top: 2rem; }

.flex-container {
  display: flex;
  flex-wrap: wrap; }

.flex-item {
  width: 100%;
  padding-left: 10px;
  padding-right: 10px; }
  .flex-item .flex-p-0 {
    padding-left: 0;
    padding-right: 0; }

.button-container {
  text-align: center; }

.btn_custom {
  border: 1px solid #ffffff;
  margin: 1rem;
  padding: .7rem 1.8rem;
  border-radius: 5px;
  cursor: pointer; }
  .btn_custom.btn_angular {
    background-color: #C4042F;
    color: #ffffff; }
  .btn_custom.btn_vue {
    background-color: #41BA82;
    color: #091B28; }
  .btn_custom.btn_react {
    background-color: #61DAFB;
    color: #091B28; }
  .btn_custom.btn_sass {
    background-color: #CE649A;
    color: #ffffff; }
  .btn_custom.btn_bootstrap {
    background-color: #563E7C;
    color: #ffffff; }

.header {
  padding: 1rem;
  background-color: #091B28; }
  .header h1 {
    color: #ffffff; }
  .header .output {
    color: #ffffff; }
    .header .output span {
      text-decoration: underline; }

@media screen and (min-width: 768px) {
  .flex-4 {
    width: 33.3%; }
  .flex-6 {
    width: 50%; }
  .flex-8 {
    width: 66.7%; }
  .form {
    margin-left: 0; } }
